  transition: transform .5s cubic-bezier(.4, 0, .2, 1), opacity .25s cubic-bezier(.4, 0, .2, 1), visibility .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
  position: fixed;
  top: 0;
  right: 0;
  overflow-y: hidden;
  transform: translateX(-0%);
}

.is-open {
  opacity: 1;
  visibility: visible;
  pointer-events: auto;
}

