.loader {
  width: 48px;
  height: 48px;
  border: 3px solid;
  border-color: var(--accent-color) rgba(0, 0, 0, 0) #fff rgba(0, 0, 0, 0);
  box-sizing: border-box;
  border-radius: 50%;
  margin-top: 50px;
  animation: rotate 1s linear infinite, bounce 2s linear infinite;
  display: inline-block;
  position: relative;
}

.loader:before, .loader:after {
  content: "";
  border: 10px solid rgba(0, 0, 0, 0);
  border-bottom-color: #fff;
  position: absolute;
  top: 0;
  left: 0;
  transform: translate(-10px, 18px)rotate(-35deg);
}

.loader:after {
  border-color: var(--accent-color) rgba(0, 0, 0, 0) rgba(0, 0, 0, 0) rgba(0, 0, 0, 0);
  transform: translate(32px, 3px)rotate(-35deg);
}

@keyframes rotate {
  100% {
    transform: rotate(360deg);
  }
}

@keyframes bounce {
  0%, 100% {
    transform: translateY(0);
  }

  50% {
    transform: translateY(-15px);
  }
}

